.schedulely {
  /* Default theme */ /* The empty... */
}
.schedulely {
  height: 100%;
  width: 100%;
  gap: 0em;
  /* Custom font must be made available via link tag, etc. */
  font-family: "Roboto", sans-serif;
  --schedulely-current-month-bg-color: white;
  --schedulely-today-indicator-color: salmon;
  --schedulely-font-color: black;
  --schedulely-sibling-month-bg-color: #eff2f1;
  --schedulely-sibling-month-font-color: dimgray;
  --schedulely-grid-gap: 0em;
  --schedulely-day-of-week-header-bg-color: black;
  --schedulely-day-of-week-header-text-color: white;
  --schedulely-day-height: 7em;
  --schedulely-border: 1px solid black;
  --schedulely-border-radius: 0.5em;
  /* The layout of the calendar body lives in a div that we use to get a ref for day height */
}
.schedulely[data-dark] {
  --schedulely-current-month-bg-color: rgb(112, 112, 112);
  --schedulely-sibling-month-bg-color: #2b2b2b;
  --schedulely-day-of-week-header-bg-color: white;
  --schedulely-day-of-week-header-text-color: black;
  --schedulely-font-color: white;
  --schedulely-border: 1px solid white;
}
.schedulely > .day-of-week-layout {
  border-radius: var(--schedulely-border-radius) var(--schedulely-border-radius) 0 0;
}
.schedulely > div > .week-container > .week-layout > div {
  border-left: none;
  border-bottom: none;
}
.schedulely > div > .week-container > .week-layout > div:first-child {
  border-left: var(--schedulely-border);
}
.schedulely > div > .week-container:last-child {
  border-radius: 0 0 var(--schedulely-border-radius) var(--schedulely-border-radius);
}
.schedulely > div > .week-container:last-child > .week-layout > div {
  border-bottom: var(--schedulely-border);
}
.schedulely > div > .week-container:last-child > .week-layout > div:first-child {
  border-radius: 0 0 0 var(--schedulely-border-radius);
}
.schedulely > div > .week-container:last-child > .week-layout > div:first-child > .default-day {
  border-radius: 0 0 0 var(--schedulely-border-radius);
}
.schedulely > div > .week-container:last-child > .week-layout > div:last-child {
  border-radius: 0 0 var(--schedulely-border-radius) 0;
}
.schedulely > div > .week-container:last-child > .week-layout > div:last-child > .default-day {
  border-radius: 0 0 var(--schedulely-border-radius) 0;
}
.schedulely[data-theme=minimal] {
  --schedulely-sibling-month-bg-color: #f1f1f1;
  --schedulely-day-of-week-header-bg-color: white;
  --schedulely-day-of-week-header-text-color: black;
  /* The layout of the calendar body lives in a div that we use to get a ref for day height */
}
.schedulely[data-theme=minimal][data-dark] {
  --schedulely-sibling-month-bg-color: #2b2b2b;
  --schedulely-day-of-week-header-bg-color: none;
  --schedulely-day-of-week-header-text-color: white;
}
.schedulely[data-theme=minimal] > .day-of-week-layout > div {
  border-left: var(--schedulely-border);
  border-right: var(--schedulely-border);
}
.schedulely[data-theme=minimal] > .day-of-week-layout > div:first-child {
  border-right: var(--schedulely-border);
}
.schedulely[data-theme=minimal] > .day-of-week-layout > div:last-child {
  border-left: var(--schedulely-border);
}
.schedulely[data-theme=minimal] > .day-of-week-layout > div {
  border-left: none;
}
.schedulely[data-theme=minimal] > .day-of-week-layout > div:last-child {
  border-left: none;
  border-right: none;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout > div {
  border-left: none;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout > div > .default-day > .week-day-layout > .default-day-header {
  text-align: right;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout > div > .default-day > .week-day-layout > .default-day-header > .default-day-header--indicator {
  margin: 1px;
  text-align: center;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout > div > .default-day > .week-day-layout > .default-day-header > .default-day-header--text {
  font-weight: normal;
  margin: 5px;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout > div:last-child {
  border-right: none;
}
.schedulely[data-theme=minimal] > div > .week-container > .week-layout:last-child > div {
  border-bottom: none;
}
.schedulely .day-of-week-layout {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  background-color: var(--schedulely-day-of-week-header-bg-color);
  color: var(--schedulely-day-of-week-header-text-color);
}
.schedulely .default-day-of-week {
  text-align: center;
}
.schedulely .event-week-layout {
  display: grid;
  grid-area: 1/1;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(4, 1fr);
  z-index: 1; /* TODO: Figure out what is making this necessary */
  gap: 0.2em; /* space between events */
  white-space: nowrap;
  grid-auto-flow: dense;
  overflow: hidden;
}
.schedulely .week-container {
  /* We use a grid here so WeekLayout and EventLayout can inheirit it's position */
  display: grid;
}
.schedulely .week-layout {
  display: grid;
  grid-area: 1/1;
  grid-template-columns: repeat(7, 1fr);
}
.schedulely .week-layout > div {
  border: var(--schedulely-border);
  position: relative;
}
.schedulely .event-position-layout {
  min-width: 0;
}
.schedulely .default-day {
  display: grid;
  height: 100%;
}
.schedulely .default-day-current {
  background-color: var(--schedulely-currentMonthBgColor);
  color: var(--schedulely-font-color);
}
.schedulely .default-day-sibling {
  background-color: var(--schedulely-sibling-month-bg-color);
  color: var(--schedulely-sibling-month-font-color);
}
.schedulely .default-day-header {
  text-align: center;
  width: 100%;
  margin-top: 0.1em;
}
.schedulely .default-day-header--text {
  position: relative;
  top: 0.15em;
  font-weight: bold;
}
.schedulely .default-day-header--indicator {
  height: 1.45em;
  width: 1.45em;
  background-color: var(--schedulely-today-indicator-color);
  border-radius: 50%;
  display: inline-block;
}
.schedulely .additional-events-indicator {
  opacity: 0.5;
  cursor: pointer;
  font-weight: bolder;
  color: var(--schedulely-font-color);
  text-align: center;
  width: fit-content;
  margin: auto;
  z-index: 1;
  margin-bottom: 0.1em;
}
.schedulely .additional-events-indicator:hover {
  opacity: 1;
}
.schedulely .event {
  border-radius: 0.3em;
  padding-left: 1em;
  font-weight: bold;
  margin-left: 0.3em;
  margin-right: 0.3em;
  box-shadow: dimgray 0.1em 0.1em 0.2em;
  text-overflow: ellipsis;
  overflow: hidden;
}
.schedulely .event:hover {
  cursor: pointer;
}
.schedulely .event-selected {
  opacity: 100%;
}
.schedulely .event-unselected {
  opacity: 80%;
}
.schedulely .header-button {
  border: none;
  background-color: transparent;
  color: var(--schedulely-font-color);
  font-size: 2em;
  opacity: 50%;
}
.schedulely .header-button:hover {
  opacity: 100%;
  cursor: pointer;
}
.schedulely .header-layout {
  display: flex;
  gap: 0.5em;
  width: 100%;
}
.schedulely .header-banner {
  flex-grow: 2;
  text-align: center;
  color: var(--schedulely-font-color);
}
.schedulely .current-month-indicator {
  background-color: salmon;
  border-radius: 20px;
  height: 0.4em;
  width: 0.4em;
  margin-left: 0.5em;
  margin-top: 0.4em;
  display: inline-block;
  position: relative;
}